
import { computed, defineComponent } from 'vue';

interface DiscountList {

				progressMoneyMin: number;	//    
				progressMoneyMax: number;	//    
				discount: number;				//  
				next: number;					//  
		
}

export default defineComponent({
	props:{
		spent: { //   
			type: Number,
			required: true,
		}
	},
	setup(props){
		
		const discountList = <DiscountList[]> [
			{
				progressMoneyMin: 0,	//    
				progressMoneyMax: 3000000,	//    
				discount: 0,				//  
				next: 12.5					//  
			
			},
			{
				progressMoneyMin: 3000000,	//    
				progressMoneyMax: 5000000,	//    
				discount: 12.5,				//  
				next: 13.5					//  
			},
			{
				progressMoneyMin: 5000000,	//    
				progressMoneyMax: 8000000,	//    
				discount: 13.5,				//  
				next: 14.4					//  
			},
			{
				progressMoneyMin: 8000000,	//    
				progressMoneyMax: 15000000,	//    
				discount: 14.4,				//  
				next: 16.3					//  
			},
			{
				progressMoneyMin: 15000000,	//    
				progressMoneyMax: Infinity,	//    
				discount: 16.3,				//  
				next: 0					//  
			}
		]


		let discount = computed<DiscountList>(() => {
			
			let d = discountList.filter(v =>  props.spent >= v.progressMoneyMin 
													&&  props.spent < v.progressMoneyMax);
			return d.length > 0 ? d[0] : discountList[0];
		})
	
		let progressInPercent =  computed(() => {
			if (discount.value.progressMoneyMax != Infinity)
				return (props.spent/discount.value.progressMoneyMax) * 100//   
			else return 100
		})
		return{
			discount,
			progressInPercent,
			ostatok: computed(() => ((discount.value.progressMoneyMax - props.spent))),
		}
	}
})

